/* Выставляем основные настройки по-умолчанию для body */
body {
  min-height: 100vh;
  margin: 0;
  font-family: "Rubik", sans-serif;
  font-size: 16px;
  line-height: 140%;
  color: #000000;
  background: #ffffff;
  scroll-behavior: smooth;
}

/* Указываем box sizing */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Убираем внутренние отступы */
ul[class],
ol[class] {
  padding: 0;
}

/* Убираем внешние отступы */
h1,
h2,
h3,
h4,
p,
ul[class],
ol[class],
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
}

/* Удаляем стандартную стилизацию для всех ul и il, у которых есть атрибут class*/
ul[class],
ol[class] {
  list-style: none;
}

/* Элементы a, у которых нет класса, сбрасываем до дефолтных стилей */
a:not([class]) {
  -webkit-text-decoration-skip: ink;
          text-decoration-skip-ink: auto;
}

/* Упрощаем работу с изображениями */
img {
  max-width: 100%;
  display: block;
}

/* Наследуем шрифты для инпутов и кнопок */
input,
button,
textarea,
select {
  font: inherit;
}

/*Обнуляем ссылки*/
a {
  text-decoration: none;
  color: #000000;
}
.container {
  max-width: 1196px;
  padding: 81px 24px 94px 24px;
  margin: 0 auto;
}

.header-title {
  font-family: "Rubik";
  font-style: normal;
  font-weight: normal;
  font-size: 32px;
  line-height: 38px;

  margin-bottom: 32px;
}

.label {
  display: block;
  height: 29px;
  padding: 0;
  margin: 0;
  font-family: "Rubik";
  font-size: 16px;
  line-height: 160%;
}

.text {
  padding: 0;
  border: 0;
  outline: 0;
  width: 100%;
}

.btn {
  border: 0;
  padding: 0;
  outline: 0;
  background-color: #FFFFFF;
}

.btn:hover {
  cursor: pointer;
}

@media (min-width: 1024px) {
