}

.vacancy {
  width: 100%;
  max-height: 728px;
  border: 1px solid #000000;
  padding: 20px 11px 19px 20px;
  margin-bottom: 24px;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

/*Блок Info*/

.info {
  margin-right: 9px;
  margin-bottom: 16px;
}

.info-img-wrapper {
  max-width: 100%;
  max-height: 59px;
  margin-bottom: 38px;
}

.info-img {
  max-width: 100%;
  max-height: 59px;
  object-fit: contain;
}

.info-text {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.info-text:not(:last-child) {
  margin-bottom: 17px;
}

.info-text__label {
  color: #545454;
}

.info-text__link,
.info-text__link:visited {
  text-decoration: none;
  color: #000000;
}

.info-text__link:hover {
  color: #a3a6a9;
}

/*Блок Description*/

.description {
  position: relative;
  display: flex;
  flex-direction: column;
}

.description__header {
  font-style: "Roboto", sans-serif;
  font-weight: normal;
  font-size: 22px;
  margin-bottom: 16px;
}

.description__text {
  max-height: 340px;
  overflow: hidden;
}

.description__text p,
.description__text h2 {
  font-family: "Roboto", sans-serif;
  font-weight: normal;
  font-size: 16px;
  line-height: 140%;
  margin-bottom: 16px;
}

.description:after {
  display: block;
  content: "";
  width: 100%;
  height: 100px;
  position: absolute;
  bottom: 0;
  background: linear-gradient(180deg, rgba(255, 255, 255, 0), #ffffff 55%);
}

.description__btn-wrapper {
  align-self: center;
  z-index: 1;
}

.description__btn {
  font-family: "Roboto", sans-serif;
  line-height: 140%;
  color: #a3a6a9;
}

/*Десктоп*/
@media (min-width: 1024px) {
  .vacancy {
    max-height: 331px;
    flex-direction: row;
    padding: 20px 21px 18px 20px;
  }

  .info {
    overflow: hidden;
  }

  .info-img-wrapper {
    max-height: 95px;
    max-width: 373px;
  }
}

/*Раскрытая вакансия*/
.vacancy__opened {
  max-height: 100%;
}

.description__opened:after {
  z-index: -1;
}

.description__text__opened {
  max-height: 100%;
  margin-bottom: 8px;
}

